.box {
  margin: 20px;
  width: 1600px;
  display: flex;
}
.box div {
  width: 70px;
  height: 70px;
  border: 0.5px solid #333;
}
.box div p {
  align-items: center;
}

.inputbox {
  display: flex;
  margin: 20px;
  
}
input {
  width: 64px;
  height: 64px;
}
.input_none {
  display: none;
}
.color_1 {
  background-color: rgb(198, 252, 213);
}
